body {
    font-family: 'Inter', sans-serif;
    margin: 0;
}

:root {
  --color-text: #493f3f;
  --color-bg: #FFE6DC;
  --color-bg-accent: #e3cdc4;
  --size: clamp(10rem, calc(1rem + 40vmin), 30rem);
  --gap: calc(var(--size) / 14);
  --duration: 60s;
  --scroll-start: 0;
  --scroll-end: calc(-100% - var(--gap));
}


@media (prefers-color-scheme: dark) {
  :root {
    --color-text: #493f3f;
    --color-bg: #FFE6DC;
    --color-bg-accent: #e3cdc4;
  }
}

/* * {
  box-sizing: border-box;
} */

.body-landingpage {
  display: grid;
  align-content: center;
  overflow: hidden;
  grid-gap: calc(clamp(10rem, calc(1rem + 40vmin), 30rem) / 14);
  gap: calc(clamp(10rem, calc(1rem + 40vmin), 30rem) / 14);
  grid-gap: var(--gap);
  gap: var(--gap);
  width: 100%;
  min-height: 100vh;
  font-family: system-ui, sans-serif;
  font-size: 1rem;
  line-height: 1.5;
  color: #493f3f;
  color: var(--color-text);
  background-color: #FFE6DC;
  background-color: var(--color-bg);
}

.marquee-landingpage {
  display: flex;
  overflow: hidden;
  -webkit-user-select: none;
          user-select: none;
  grid-gap: calc(clamp(10rem, calc(1rem + 40vmin), 30rem) / 14);
  gap: calc(clamp(10rem, calc(1rem + 40vmin), 30rem) / 14);
  grid-gap: var(--gap);
  gap: var(--gap);
  -webkit-mask-image: linear-gradient(
    to right,
    hsl(0 0% 0% / 0),
    hsl(0 0% 0% / 1) 20%,
    hsl(0 0% 0% / 1) 80%,
    hsl(0 0% 0% / 0)
  );
          mask-image: linear-gradient(
    to right,
    hsl(0 0% 0% / 0),
    hsl(0 0% 0% / 1) 20%,
    hsl(0 0% 0% / 1) 80%,
    hsl(0 0% 0% / 0)
  );
  -webkit-mask-image: linear-gradient(
    var(--mask-direction, to right),
    hsl(0 0% 0% / 0),
    hsl(0 0% 0% / 1) 20%,
    hsl(0 0% 0% / 1) 80%,
    hsl(0 0% 0% / 0)
  );
          mask-image: linear-gradient(
    var(--mask-direction, to right),
    hsl(0 0% 0% / 0),
    hsl(0 0% 0% / 1) 20%,
    hsl(0 0% 0% / 1) 80%,
    hsl(0 0% 0% / 0)
  );
}

.marquee__group {
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: space-around;
  grid-gap: calc(clamp(10rem, calc(1rem + 40vmin), 30rem) / 14);
  gap: calc(clamp(10rem, calc(1rem + 40vmin), 30rem) / 14);
  grid-gap: var(--gap);
  gap: var(--gap);
  min-width: 100%;
  -webkit-animation: scroll-x 60s linear infinite;
          animation: scroll-x 60s linear infinite;
  -webkit-animation: scroll-x var(--duration) linear infinite;
          animation: scroll-x var(--duration) linear infinite;
}

@media (prefers-reduced-motion: reduce) {
  .marquee__group {
    -webkit-animation-play-state: paused;
            animation-play-state: paused;
  }
}

.marquee--vertical {
  --mask-direction: to bottom;
}

.marquee--vertical,
.marquee--vertical .marquee__group {
  flex-direction: column;
}

.marquee--vertical .marquee__group {
  -webkit-animation-name: scroll-y;
          animation-name: scroll-y;
}

.marquee--reverse .marquee__group {
  animation-direction: reverse;
  -webkit-animation-delay: -3s;
          animation-delay: -3s;
}


@-webkit-keyframes scroll-x {
  from {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    -webkit-transform: translateX(var(--scroll-start));
            transform: translateX(var(--scroll-start));
  }
  to {
    -webkit-transform: translateX(calc(-100% - calc(clamp(10rem, calc(1rem + 40vmin), 30rem) / 14)));
            transform: translateX(calc(-100% - calc(clamp(10rem, calc(1rem + 40vmin), 30rem) / 14)));
    -webkit-transform: translateX(var(--scroll-end));
            transform: translateX(var(--scroll-end));
  }
}


@keyframes scroll-x {
  from {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    -webkit-transform: translateX(var(--scroll-start));
            transform: translateX(var(--scroll-start));
  }
  to {
    -webkit-transform: translateX(calc(-100% - calc(clamp(10rem, calc(1rem + 40vmin), 30rem) / 14)));
            transform: translateX(calc(-100% - calc(clamp(10rem, calc(1rem + 40vmin), 30rem) / 14)));
    -webkit-transform: translateX(var(--scroll-end));
            transform: translateX(var(--scroll-end));
  }
}

@-webkit-keyframes scroll-y {
  from {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    -webkit-transform: translateY(var(--scroll-start));
            transform: translateY(var(--scroll-start));
  }
  to {
    -webkit-transform: translateY(calc(-100% - calc(clamp(10rem, calc(1rem + 40vmin), 30rem) / 14)));
            transform: translateY(calc(-100% - calc(clamp(10rem, calc(1rem + 40vmin), 30rem) / 14)));
    -webkit-transform: translateY(var(--scroll-end));
            transform: translateY(var(--scroll-end));
  }
}

@keyframes scroll-y {
  from {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    -webkit-transform: translateY(var(--scroll-start));
            transform: translateY(var(--scroll-start));
  }
  to {
    -webkit-transform: translateY(calc(-100% - calc(clamp(10rem, calc(1rem + 40vmin), 30rem) / 14)));
            transform: translateY(calc(-100% - calc(clamp(10rem, calc(1rem + 40vmin), 30rem) / 14)));
    -webkit-transform: translateY(var(--scroll-end));
            transform: translateY(var(--scroll-end));
  }
}


/* Element styles */
.marquee-landingpage svg {
  display: grid;
  place-items: center;
  width: clamp(10rem, calc(1rem + 40vmin), 30rem);
  width: var(--size);
  fill: #493f3f;
  fill: var(--color-text);
  background: #e3cdc4;
  background: var(--color-bg-accent);
  aspect-ratio: 16/9;
  padding: calc(clamp(10rem, calc(1rem + 40vmin), 30rem) / 10);
  padding: calc(var(--size) / 10);
  border-radius: 0.5rem;
}

.marquee--vertical svg {
  aspect-ratio: 1;
  width: calc(clamp(10rem, calc(1rem + 40vmin), 30rem) / 1.5);
  width: calc(var(--size) / 1.5);
  padding: calc(clamp(10rem, calc(1rem + 40vmin), 30rem) / 6);
  padding: calc(var(--size) / 6);
}

/* Parent wrapper */
.wrapper-landingpage {
  display: flex;
  flex-direction: column;
  grid-gap: calc(clamp(10rem, calc(1rem + 40vmin), 30rem) / 14);
  gap: calc(clamp(10rem, calc(1rem + 40vmin), 30rem) / 14);
  grid-gap: var(--gap);
  gap: var(--gap);
  margin: auto;
  max-width: 100vw;
}

.wrapper--vertical {
  flex-direction: row;
  height: 100vh;
}



