}

.getstartbutton span {
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.5s;
}

.getstartbutton span:after {
  content: '\BB';
  position: absolute;
  opacity: 0;
  top: 0;
  right: -20px;
  transition: 0.5s;
}

.getstartbutton:hover span {
  padding-right: 25px;
}

.getstartbutton:hover span:after {
  opacity: 1;
  right: 0;
}


