/* Dashboard Carousel - React Slick version */

.Dashboard-Carousel {
    width: 95%;
    margin: 0 auto;
    /* align-items: flex-start; */
    /* margin-top: 0; */
}
  
.slick-slide > div {
    margin: 0 10px;
}
.slick-list {
    margin: 0 -10px;
    height: 290px;
}

.slick-track {
    float: left;
    margin-top: 15px;
    margin: 15px;
}

.slick-prev::before,
.slick-prev::after {
    color: #000000 !important;
}

.slick-next::before,
.slick-next::after {
    color:#000000 !important;
}

.Dashboard-item-card {
    border: 1px solid #fff;
    background: #FFE6DC;
    border-radius: 8px;
    overflow: hidden;

    height: 250px;
    color: black;
    cursor: pointer;
}

.Dashboard-item-card:hover {
    transition: -webkit-transform .5s;
    transition: transform .5s;
    transition: transform .5s, -webkit-transform .5s; 
    -webkit-transform: scale(1.1); 
            transform: scale(1.1);   
} 

.dashboard-item-card-top h1 {
    text-align: center;
    align-items: center;
    font-size: 1.1rem;
    vertical-align: middle;
    margin: 5%;
}

.dashboard-item-card-top > img {
    width: 100%;
    height: 180px;
    object-fit: cover;
    background: white;
}

.dashboard-item-card-top img {

    max-width: 100%;
    max-height: 100%;
}

.dashboard-item-card-bottom {
    margin: 10px 20px;
}

.dashboard-item-card-bottom h3 {
    text-align: center;
}

.dashboard-item-category {
    position: relative;
}

.dashboard-item-category::before {
    content: '';
    background: rgb(255, 61, 61);
    position: absolute;
    top: calc(100% + 10px);
    left: 0;
    right: 0;
    height: 2px;
}


